import { Stack, Typography } from '@mui/material';
import './EcoCampaignCard.scss';
import { colors } from './../../../../../utils/colors';
import EcoInitiativeCampaignBrand from '../EcoInitiativeCampaignBrand/EcoInitiativeCampaignBrand';

export default function EcoCampaignCard() {
    
    return (
        <Stack className="campaignCardContainer">
            <Typography className="fs-16 ff-Roboto fw-regular" style={{color: colors.primaryHover, marginBottom: '9px'}}>Bring back to recycle</Typography>
            <Stack direction="row" justifyContent="space-between">
                <Stack flex={1}>
                    <EcoInitiativeCampaignBrand />
                </Stack>
                <Stack flex={1}>
                <Typography className="fs-14 ff-Roboto fw-regular" style={{color: colors.black}}>Mobile Phone</Typography>  
                </Stack>
            </Stack>

            <Stack direction="row" justifyContent="space-between" style={{marginTop: '6px'}}>
            <Stack flex={1} direction="row">
                <div className='status-circle'></div>
                <Typography className="fs-10 ff-Roboto fw-medium" style={{color: colors.black, textTransform: 'uppercase'}}>Active</Typography>  
            </Stack>
            <Stack flex={1}></Stack>
            </Stack>
        </Stack>
    );
}