import { useState } from "react";
import {
  Box,
  Button,
  Dialog,
  DialogTitle,
  IconButton,
  TextField,
  Typography,
  useMediaQuery,
} from "@mui/material";
import { Stack } from "@mui/system";
import { Form, Formik } from "formik";
import * as yup from "yup";
import CloseIcon from "@mui/icons-material/Close";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";

import "./EditEcoCampaign.scss";
import { colors } from "./../../../../../utils/colors";
import { buttonStyle2 } from "../../../../../utils/commonStyle";
import EcoCampaignDetails from "./componets/EcoCampaignDetails";
import EcoCampaignSetting from "./componets/EcoCampaignSetting";
import EcoCampaignAdditionalInfo from "./componets/EcoCampaignAdditionalInfo";

export default function EditEcoCampaign({ open, handleClose, template }) {
  const isMobile = useMediaQuery("(max-width:1000px)");

  /**
   * dialog style
   */
  const sx = {
    "& .MuiDialog-container": {
      alignItems: "end",
    },
    "& .MuiPaper-root": {
      width: "728px",
      minHeight: isMobile ? "620px" : "656px",
      maxHeight: isMobile ? "620px" : "656px",
      margin: isMobile ? "0" : "8px",
      padding: "0 0px 16px 16px",
      borderRadius: isMobile ? "16px 16px 0 0" : "8px",
    },
  };

  const [activeMenu, setActiveMenu] = useState(0);

  const values = {
    ecoInitiativeTemplateId: '',
    title: "",
    shortDescription: "",
    startDate: null,
    endDate: null,
    ecoPoints: "",
    ecoPointsType: '',
    ecoRedemptionType: '',
    itemSku: [
      {
        sku: "",
      },
    ],
    brandLogo: "",
    brandName: "",
    subBrandName: "",
    product: "",
    country: "",
    cityId: "",
    detailedDescription: ''
  };

  const initialValues = {
    ...values, 
    ...template,
    ecoPoints: template?.ecoPoints ? template?.ecoPoints : template?.defaultPoints,
    ecoPointsType: template?.ecoPointsType ? template?.ecoPointsType : template?.pointsType,
    ecoRedemptionType: template?.ecoRedemptionType ? template?.ecoRedemptionType : template?.pointsRedemption,
  };

  const SideToggleButton = ({ label, isActive = false, onMenuChange }) => {
    const handleMenuChange = () => {
      if (!isActive) {
        onMenuChange();
      }
    };

    return (
      <Stack
        onClick={handleMenuChange}
        className={`sideToggleBtn ${isActive ? "btn-active" : ""}`}
        direction="row"
        alignItems="center"
        justifyContent="space-between"
      >
        <Typography
          style={{ color: colors.blackMain }}
          className="fs-14 ff-Roboto fw-medium"
        >
          {label}
        </Typography>
        <KeyboardArrowRightIcon
          className="arrow"
          style={{ color: colors.blackMain }}
        />
      </Stack>
    );
  };

  const save = (values) => {};

  return (
    <Dialog
      sx={sx}
      open={open}
      onClose={handleClose}
      maxWidth="md"
      aria-labelledby="edit-eco-campaign"
      aria-describedby="edit-eco-campaign-description"
    >
      <DialogTitle
        className="fs-16 ff-Roboto fw-bold"
        sx={{
          borderBottom: 1,
          borderBottomColor: "#474747",
        }}
      >
        EDIT Campaign
        <IconButton
          aria-label="close"
          onClick={handleClose}
          sx={{
            position: "absolute",
            right: 8,
            top: 8,
            color: colors.black,
          }}
        >
          <CloseIcon fontSize="16px" />
        </IconButton>
      </DialogTitle>
      <Stack>
        <Stack direction="row" sx={{ mt: "16px" }}>
          <Stack flex={"30%"}>
            {["Details", "Settings", "Additional Info"].map((label, index) => (
              <SideToggleButton
                key={index}
                label={label}
                isActive={index === activeMenu}
                onMenuChange={() => setActiveMenu(index)}
              />
            ))}
          </Stack>
          <Stack flex={"70%"} className="sideBodyContainer">
            <Formik
              initialValues={initialValues}
              enableReinitialize={true}
              validationSchema={yup.object().shape({
                startDate: yup
                  .string("Enter your StartDate")
                  .required("Start Date is Required"),
                endDate: yup
                  .string("Enter your EndDate")
                  .required("End Date is Required"),
                ecoPoints: yup
                  .string("Enter your Email")
                  .required("Eco Points is Required"),
                itemSku: yup.array().of(
                  yup.object().shape({
                    sku: yup
                      .string("Enter your sku")
                      .required("sku is Required"),
                  })
                ),
              })}
              onSubmit={(values) => save(values)}
            >
              {(propsFormik) => (
                <Form noValidate autoComplete="off">
                  <>
                    {(activeMenu === 0 || isMobile) && <EcoCampaignDetails />}
                    {(activeMenu === 1 || isMobile) && <EcoCampaignSetting />}
                    {(activeMenu === 2 || isMobile) && (
                      <EcoCampaignAdditionalInfo />
                    )}
                  </>
                </Form>
              )}
            </Formik>

            <Stack
              direction="row"
              justifyContent="flex-end"
              flexGrow={1}
              alignItems="flex-end"
              sx={{ mt: "16px" }}
            >
              <Button
                sx={{
                  ...buttonStyle2,
                  width: activeMenu === 2 ? "100%" : "160px",
                }}
                onClick={() => {}}
                variant="contained"
              >
                {activeMenu === 2 ? "CREATE" : "SAVE"}
              </Button>
            </Stack>
          </Stack>
        </Stack>
      </Stack>
    </Dialog>
  );
}
