import "./EcoTemplateCard.scss";
import React from "react";
import { Button, Card, Stack, Typography, useMediaQuery } from "@mui/material";
import { colors } from "./../../../../../utils/colors";
import { buttonStyle2 } from "../../../../../utils/commonStyle";

import refuse from '../../../../../assets/images/refuse.svg';
import recycle from '../../../../../assets/images/recycle.svg';
import reuse from '../../../../../assets/images/reuse.svg';

import ecoTemplateBg from '../../../../../assets/images/eco-template-bg.svg';

export default function EcoTemplateCard({template, actNow, itemId}) {
  const isMobile = useMediaQuery("(max-width:600px)");

  const categoryImg = template?.categoryType === 'reuse' ? reuse : template?.categoryType === 'recycle' ? recycle : refuse;

  return (
    <Card
      variant="outlined"
      className="ecoTemplateCard flex-column"
      sx={{ width: isMobile ? 'auto' : 272}}
      style={isMobile ?{ height: '152px', marginBottom: 14}: {marginBottom: 14}}
    >
      <Stack direction="row">
        <Typography className="ff-Roboto" style={{ color: colors.deactive }}>
          {template?.title}
        </Typography>
      </Stack>
      <Stack direction="row" flex={1} alignItems="flex-end" justifyContent={"flex-end"}>
        <Button onClick={actNow}  aria-label="act-now" variant="contained" className="fw-medium" sx={{...buttonStyle2, width: 88, height: 32, fontSize: 10, zIndex: 1}}>Act Now</Button>
      </Stack>
      <img className="categoryImg" src={categoryImg} alt={'categoryImg'} />
      <img className="eco-template-bg" src={ecoTemplateBg} alt={'bg'} />
    </Card>

    
  );
}
