import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from "@mui/material";
import "./EcoStatusConfirmationDialog.scss";
import { colors } from './../../../../../utils/colors';

export default function EcoStatusConfirmationDialog({ open, handleClose, title, subTitle, btnType }) {
    const btnColor = btnType === 'warn' ? '#C71212' : '#8AC865';
  return <Dialog open={open} onClose={handleClose} maxWidth="xs" aria-labelledby="alert-dialog-title"
  aria-describedby="alert-dialog-description">
    <DialogTitle style={{color: btnType === 'warn' ? colors.dangerDark : colors.blackMain}} className="ff-Roboto fs-16 fw-medium" id="alert-dialog-title">
          {"Do you want to start editing?"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description" style={{color: colors.blackMain}} className="ff-Roboto fs-12 fw-regular">
          You can save your changes while editing the campaigns and come back later to finish it.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button className="ff-Roboto fs-14 fw-medium cancelBtn" variant="outlined" onClick={handleClose}>CANCEL</Button>
          <Button onClick={() => handleClose(true)} className="ff-Roboto fs-14 fw-medium successBtn" variant="contained" style={{backgroundColor: btnColor}} >EDIT</Button>
        </DialogActions>
  </Dialog>;
}
