import { Box, Stack, TextField, Typography } from "@mui/material";
import { colors } from "../../../../../../utils/colors";
import React from "react";
import { useFormikContext } from "formik";

export default function EcoCampaignDetails() {
  const {
    values,
    touched,
    errors,
    handleChange,
    handleBlur,
    isValid,
    dirty,
    setFieldValue,
  } = useFormikContext();

  return (
    <React.Fragment>
      <Stack sx={{ mb: "22px" }}>
        <Typography
          style={{ color: colors.blackMain }}
          className="fs-14 ff-Roboto fw-medium mb-7"
        >
          Name*
        </Typography>
        <TextField
          fullWidth
          autoComplete="off"
          id="title"
          name="title"
          value={values.title || ""}
          onChange={handleChange}
          onBlur={handleBlur}
        />
      </Stack>
      <Stack sx={{ mb: "22px" }}>
        <Typography
          style={{ color: colors.blackMain }}
          className="fs-14 ff-Roboto fw-medium mb-7"
        >
          Short Description (30 chars)
        </Typography>
        <TextField
          fullWidth
          autoComplete="off"
          id="shortDescription"
          name="shortDescription"
          value={values.shortDescription || ""}
          onChange={handleChange}
          onBlur={handleBlur}
          max={30}
        />
      </Stack>
      <Stack sx={{ mb: "22px" }}>
        <Typography
          style={{ color: colors.blackMain }}
          className="fs-14 ff-Roboto fw-medium mb-7"
        >
          Detailed Description (500 chars)
        </Typography>
        <Box
          style={{
            padding: "16px",
            backgroundColor: "rgba(73, 96, 115, 0.11)",
            minHeight: '176px'
          }}
        >
          <Typography>
            {values?.detailedDescription}
          </Typography>
        </Box>
      </Stack>
    </React.Fragment>
  );
}
