import React, { useState } from "react";
import {
  Stack,
  Typography,
  TextField,
  useMediaQuery,
  DialogActions,
  Button,
  InputAdornment,
  IconButton,
} from "@mui/material";

import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import AddIcon from "@mui/icons-material/Add";
import ClearIcon from "@mui/icons-material/Clear";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DatePicker, DesktopDatePicker } from "@mui/x-date-pickers";
import { AdapterMoment } from "@mui/x-date-pickers/AdapterMoment";
import moment from "moment";
import { FieldArray, useFormikContext } from "formik";
import { colors } from "../../../../../../utils/colors";
import { buttonStyle3 } from "../../../../../../utils/commonStyle";

export default function EcoCampaignSetting() {
  const isMobile = useMediaQuery("(max-width:1000px)");

  const [data, setData] = useState({
    startDate: null,
    endDate: null,
    ecoPoints: "20",
    itemSku: [
      {
        sku: "11",
      },
    ],
  });

  const {
    values,
    touched,
    errors,
    handleChange,
    handleBlur,
    isValid,
    dirty,
    setFieldValue,
  } = useFormikContext();

  return (
    <Stack>
      <Typography
        className="fs-14 ff-Roboto fw-medium mb-7"
        color={colors.blackMain}
      >
        Schedule
      </Typography>
      <Stack
        direction="row"
        justifyContent="space-between"
        className="datepickerSetting"
      >
        <LocalizationProvider dateAdapter={AdapterMoment}>
          <DesktopDatePicker
            label="Start Date"
            id="startDate"
            name="startDate"
            value={values.startDate}
            onChange={(newValue) => {
              setFieldValue("startDate", newValue);
            }}
            PopperProps={{
              placement: "bottom-start",
            }}
            disablePast={true}
            components={{
              OpenPickerIcon: ArrowDropDownIcon,
            }}
            renderInput={(params) => (
              <TextField style={{ width: "240px" }} {...params} />
            )}
          />
        </LocalizationProvider>
        <div className="endDatePicker">
          <LocalizationProvider dateAdapter={AdapterMoment}>
            <DatePicker
              label="End Date"
              id="endDate"
              name="endDate"
              value={values.endDate}
              onChange={(newValue) => {
                setFieldValue("endDate", newValue);
              }}
              PopperProps={{
                placement: "bottom-end",
              }}
              minDate={moment(values.startDate).add(1, "d")}
              disabled={values.startDate == null ? true : false}
              components={{
                OpenPickerIcon: ArrowDropDownIcon,
              }}
              renderInput={(params) => (
                <TextField
                  style={{ width: "240px", height: "100%" }}
                  {...params}
                />
              )}
            />
          </LocalizationProvider>
        </div>
      </Stack>
      <Stack className="mt-14">
        <Typography
          className="fs-14 ff-Roboto fw-medium mb-7"
          color={colors.blackMain}
        >
          Eco Points
        </Typography>
        <TextField
          fullWidth
          id="ecoPoints"
          name="ecoPoints"
          value={values.ecoPoints}
          onChange={handleChange}
          onBlur={handleBlur}
          disabled={values?.ecoPointsType === 'fixed'}
        />
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
          className="mt-6"
        >
          <Typography
            className="fs-12 ff-Roboto fw-medium"
            color={colors.blackMain}
          >
            One Time / Item or Cart 
          </Typography>
          <Typography
            className="fs-12 ff-Roboto fw-medium"
            color={colors.blackMain}
          >
            Fixed / Flexible
          </Typography>
        </Stack>
      </Stack>
      <Typography
        className="fs-14 ff-Roboto fw-medium mt-14 mb-7"
        color={colors.blackMain}
      >
        Item SKUs
      </Typography>
      <FieldArray name="itemSku">
        {({ push, remove }) => (
          <>
            <div>
              {values.itemSku.map((p, index) => {
                const sku = `itemSku[${index}].sku`;
                return (
                  <Stack key={index} style={{ marginBottom: "8px" }}>
                    <TextField
                      fullWidth
                      id={sku}
                      name={sku}
                      value={p.sku}
                      onChange={handleChange}
                      autoComplete="off"
                      InputProps={{
                        endAdornment: (
                          <InputAdornment position="end">
                            {values.itemSku.length > 1 && (
                              <IconButton
                                onClick={() => remove(index)}
                                style={{
                                  transform: "scale(1.143)",
                                  background: "rgb(237, 237, 237)",
                                  borderRadius: "4px",
                                }}
                              >
                                <ClearIcon />
                              </IconButton>
                            )}
                          </InputAdornment>
                        ),
                      }}
                    />
                  </Stack>
                );
              })}
            </div>
            <div style={{ textAlign: "center" }}>
              <Button
                sx={buttonStyle3}
                onClick={() =>
                  push({
                    sku: "",
                  })
                }
                variant="contained"
                endIcon={<AddIcon />}
              >
                ADD
              </Button>
            </div>
          </>
        )}
      </FieldArray>
    </Stack>
  );
}
