import { Stack } from "@mui/material";
import EcoCampaignCard from "../EcoCampaignCard/EcoCampaignCard";

export default function EcoCampaignListView({ ecoInitiatives }) {

    return (
        <Stack>
            {ecoInitiatives.map((item, index) => <EcoCampaignCard key={index} />)}
        </Stack>
    );
}