import React from "react";
import {
  Stack,
  useMediaQuery,
  Button,
  Typography,
  TextField,
  MenuItem,
} from "@mui/material";
import { useFormikContext } from "formik";
import { useSelector } from "react-redux";
import { colors } from "../../../../../../utils/colors";
import {
  dialogButtonStyle,
  buttonStyle2,
} from "../../../../../../utils/commonStyle";
import DragAndDrop from "./DragAndDrop";
import { useState } from "react";

export default function EcoCampaignAdditionalInfo() {
  const isMobile = useMediaQuery("(max-width:1000px)");

  const [data, setData] = useState({
    imgUrl: '',
    brand: '',
    subBrand: '',
    product: '',
    cityId: '',
    country: ''
})

  const { citiesList } = useSelector((state) => state.app);

  const {
    values,
    touched,
    errors,
    handleChange,
    handleBlur,
    isValid,
    dirty,
    setFieldValue,
  } = useFormikContext();

  //handle upload will do the upload in aws
  const handleUploadClick = (e) => {
    console.log(e);
  };

  //This will have files after drag and drop or click input
  // I have updated the state imgUrl with files.name. please change it to files so you can upload it to aws
  const handleFile = (files) => {
    if (files) {
      setFieldValue("brandLogo", files.name);
    } else {
      setFieldValue("brandLogo", "");
    }
  };

  return (
    <Stack>
      <DragAndDrop
        id="brandLogo"
        handleFile={(files) => handleFile(files)}
      />
      <Button
        disabled={!data.imgUrl}
        onClick={handleUploadClick}
        sx={{ ...dialogButtonStyle, marginTop: "8px", width: "100%" }}
        variant="contained"
      >
        Upload
      </Button>
      <Stack
        className="mt-16"
        direction="row"
        justifyContent="space-between"
        alignItems="center"
        spacing="8px"
      >
        <Stack flex={1}>
          <Typography
            className="fs-14 ff-Roboto fw-medium mb-7"
            color={colors.blackMain}
          >
            Brand
          </Typography>
          <TextField
            fullWidth
            id="brandName"
            name="brandName"
            value={values.brandName || ""}
            onChange={handleChange}
            onBlur={handleBlur}
          />
        </Stack>
        <Stack flex={1}>
          <Typography
            className="fs-14 ff-Roboto fw-medium mb-7"
            color={colors.blackMain}
          >
            Sub Brand
          </Typography>
          <TextField
            fullWidth
            id="subBrandName"
            name="subBrandName"
            value={values.subBrandName || ""}
            onChange={handleChange}
            onBlur={handleBlur}
          />
        </Stack>
      </Stack>
      <Stack className="mt-16">
        <Typography
          className="fs-14 ff-Roboto fw-medium mb-7"
          color={colors.blackMain}
        >
          Product
        </Typography>
        <TextField
          fullWidth
          id="product"
          name="product"
          value={values.product || ""}
          onChange={handleChange}
          onBlur={handleBlur}
        />
      </Stack>
      <Stack className="mt-16">
        <Typography
          className="fs-14 ff-Roboto fw-medium mb-7"
          color={colors.blackMain}
        >
          City
        </Typography>
        <TextField
          fullWidth
          id="cityId"
          name="cityId"
          value={values.cityId || ""}
          onChange={(e) => {
            handleChange(e);
            setData((prev) => ({ ...prev, cityId: e.target.value }));
          }}
          onBlur={handleBlur}
          select
        >
          {citiesList.map((item) => (
            <MenuItem key={item.cityId} value={item.cityId}>
              {" "}
              {item.cityName}{" "}
            </MenuItem>
          ))}
        </TextField>
      </Stack>
      <Stack className="mt-16">
        <Typography
          className="fs-14 ff-Roboto fw-medium mb-7"
          color={colors.blackMain}
        >
          Country
        </Typography>
        <TextField
          fullWidth
          id="country"
          name="country"
          value={values.country || ""}
          onChange={handleChange}
          onBlur={handleBlur}
        />
      </Stack>
    </Stack>
  );
}
