import './EcoCampaignTableView.scss';
import { Button, IconButton, Stack, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from "@mui/material";

import moment from "moment";

import MoreVertIcon from "@mui/icons-material/MoreVert";
import AccessTimeIcon from "@mui/icons-material/AccessTime";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";

import refuse from "../../../../../assets/images/refuse.svg";
import { colors } from "../../../../../utils/colors";
import EcoInitiativeCampaignBrand from "../EcoInitiativeCampaignBrand/EcoInitiativeCampaignBrand";

const headerCell = [
    {
      name: "Initiative",
      width: "30",
    },
    {
      name: "Created By",
      width: "10%",
      key: 'createdBy'
    },
    {
      name: "Created Date",
      width: "10%",
      key: 'createdAt',
      type: 'date'
    },
    {
      name: "Status",
      width: "12%",
    },
    {
      name: "Recent Activity",
      width: "10%",
      key: 'updatedAt',
      type: 'date'
    },
    {
      name: "Brand",
      width: "14%",
    },
    {
      name: "Product",
      width: "14%",
      key: 'product'
    },
  ];
  

export default function EcoCampaignTableView({ecoInitiatives, onStatus}) {
  /**
   *
   * @returns Header of the Table
   */
  const EnchanedTableHeader = () => {
    return (
      <TableHead className="mainUserTableHead">
        <TableRow
          sx={{
            "& th": {
              backgroundColor: colors.secondary,
            },
          }}
        >
          {headerCell?.map((header, index) => (
            <TableCell
              key={index}
              style={{ width: header.width, paddingLeft: index === 0 ? 16 : 0 }}
            >
              <Stack
                direction="row"
                alignItems="center"
                justifyContent={
                  index === headerCell.length - 1
                    ? "space-between"
                    : "flex-start"
                }
              >
                <Typography
                  className="ff-Roboto fs-12 fw-semibold"
                  style={{ color: colors.primaryDark }}
                >
                  {header.name}
                </Typography>
                {index === headerCell.length - 1 && (
                  <IconButton
                    style={{ color: colors.primaryDark }}
                    aria-label="moreColumn"
                    component="label"
                  >
                    <MoreVertIcon />
                  </IconButton>
                )}
              </Stack>
            </TableCell>
          ))}
        </TableRow>
      </TableHead>
    );
  };

  /**
   *
   * @returns title of the EcoInitiatives
   */
  const EcoTitle = ({campaign}) => {
    return (
      <Stack direction="row" style={{ height: "100%" }}>
        <Stack justifyContent="flex-end" sx={{ width: 96 }}>
          <img
            className="ecoInitoatives-category-img"
            src={refuse}
            alt="Category"
          />
        </Stack>
        <Stack
          style={{ paddingLeft: "24px" }}
          justifyContent="center"
          className="row-hover"
          flexGrow={1}
        >
          <Typography
            style={{ color: colors.deactive }}
            className="ff-Roboto fw-bold ellipseStyle capitalize"
          >
            {campaign?.title}
          </Typography>
          <Stack direction="row" alignItems="center">
            <AccessTimeIcon fontSize="14px" style={{ marginRight: "8px" }} />
            <Typography className="fs-12 ff-Roboto fw-regular ellipseStyle">
              1 month
            </Typography>
          </Stack>
        </Stack>
      </Stack>
    );
  };

  /**
   *
   * @returns barnd of the Campaign Status
   */

  const CampaignStatus = ({ status, onChangeStatus }) => {
    let activeClass = "";
    if (status === "draft") {
      activeClass = "draft";
    }
    if (status === 'scheduled') {
      activeClass = "otherStatus scheduled";
    }
    if (status === 'running') {
      activeClass = "otherStatus running";
    }
    if (status === 'paused' || status === 'cancelled' || status === 'not yet') {
      activeClass = "otherStatus paused";
    }
    return (
      <Stack
        direction="row"
        style={{ height: "100%" }}
        alignItems="center"
        className="row-hover"
      >
        <Button
          className={`campaignStatusButton fs-12 ff-Roboto fw-regular ${activeClass}`}
          variant="outlined"
          onClick={onChangeStatus}
        ></Button>
        {/* <Typography style={{color: colors.primaryHover}}  className="fs-12 ff-Roboto fw-regular ">Finished</Typography> */}
      </Stack>
    );
  };



  return (
    <TableContainer>
      <Table
        style={{ tableLayout: "fixed" }}
        stickyHeader
        aria-label="sticky table"
        sx={{ minWidth: 650 }}
      >
        <EnchanedTableHeader />
        <TableBody
          className="mainUserBodyTable"
          sx={{ backgroundColor: "transparent" }}
        >
          {ecoInitiatives.map((item, index) => (
            <TableRow className="tableRowHover" key={index}>
              {headerCell?.map((header, headerIndex) => (
                <TableCell
                  key={headerIndex}
                  style={{
                    width: header.width,
                    height: 80,
                    borderBottomWidth: 0,
                  }}
                >
                  <Stack
                    style={{
                      marginTop: "8px",
                      backgroundColor: "#fff",
                      height: "72px",
                      position: "relative",
                    }}
                  >
                    {header.name === "Initiative" && <EcoTitle campaign={item} />}
                    {header.name === "Brand" && <EcoInitiativeCampaignBrand campaign={item} />}
                    {header.name === "Status" && (
                      <CampaignStatus
                      status={item?.campaingStatus}
                        onChangeStatus={() => onStatus(item)}
                      />
                    )}
                    {!["Initiative", "Brand", "Status"].includes(
                      header.name
                    ) && (
                      <Stack
                        sx={{ height: "100%" }}
                        justifyContent="center"
                        className="row-hover"
                      >
                        <Typography
                          className="ff-Roboto fs-14 "
                          style={{ color: colors.black }}
                        >
                          { header?.type === 'date' ? moment(item[header.key]).format('DD-MM-YYYY') :item[header.key] || '-'}
                        </Typography>
                      </Stack>
                    )}
                    {headerIndex === headerCell.length - 1 && (
                      <Stack
                        className="actionContainer"
                        alignItems="center"
                        justifyContent="center"
                      >
                        <IconButton
                          style={{
                            color: colors.primaryDark,
                            fontSize: "16px",
                          }}
                          aria-label="contentDuplicate"
                          component="label"
                        >
                          <ContentCopyIcon
                            fontSize="16px"
                            style={{ color: colors.primaryHover }}
                          />
                        </IconButton>
                      </Stack>
                    )}
                  </Stack>
                </TableCell>
              ))}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
